import { ListHotels } from "./listHotels";

export class Form {
  constructor(form) {
    this.form = form;
    this.button = document.querySelector("#btnSubmit");
    this.listHotels = document.querySelector("#listHotels");

    this._handleSubmitBtn = this._submit.bind(this);

    this._init();
  }

  _init() {
    this.button.addEventListener("click", this._handleSubmitBtn);
  }

  _createIndexDate() {
    const index = new Date().getTime();

    return index;
  }

  _createLocalDate() {
    const date = new Date();

    return (
      date.toLocaleDateString() + " " + date.toLocaleTimeString().slice(0, -3)
    );
  }

  _send(data) {
    const url = "/api/data";

    fetch(url, {
      method: "POST",
      headers: { "Content-Type": "application/json;charset=UTF-8" },
      body: JSON.stringify(data),
    })
      .then((response) => response.json())
      .then((data) => new ListHotels(this.listHotels, data.list))
      .catch((error) => console.error(error));
  }

  _submit(event) {
    event.preventDefault();

    if (!this.form.checkValidity()) {
      this.form.classList.add("invalid");
    } else {
      this.form.classList.remove("invalid");

      const indexDate = this._createIndexDate();
      const dateTime = this._createLocalDate();

      const formData = new FormData(this.form);
      formData.append("id", indexDate);
      formData.append("date", dateTime);

      const data = {};

      for (let [name, value] of formData) {
        data[name] = value;
      }

      this._send(data);

      this.form.reset();
    }
  }
}
